.wrapper {
    width: 50px;
    height: 50px;
    background-color: #E6EAED;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 86%;
    margin: 7%;
    border-radius: 10px;
    border: none;
    line-height: 10px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7), -1px -1px 2px rgba(255, 255, 255), 3px 3px 7px rgba(255, 255, 255, 0.4) inset, -5px -5px 10px rgba(0, 0, 0, 0.2) inset;
}

.content::selection {
    background-color: rgba(0, 0, 0, 0);
}

.none {
    background-color: #d8d8d8;
}

.red {
    background-color: rgb(255, 128, 128);
}

.orange {
    background-color: rgb(255, 189, 128);
}

.yellow {
    background-color: rgb(255, 236, 128);
}

.green {
    background-color: rgb(164, 236, 155);
}

.blue {
    background-color: rgb(118, 118, 255);
}

.mover {
    background-color: rgba(255, 174, 0, 0.3);
}

span {
    padding: 3px;
}